  display: flex;
  /* grid-template-columns: repeat(2, 1fr); */
  grid-gap: 2em;
  gap: 2em;
  flex-wrap: wrap;
  grid-area: 1/1 / span 4 / span 4;
}
.footer-wrapper .r1 h3 {
  color: #fff !important;
}
.footer-wrapper .r2 {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-area: 5/1 / span 1 / span 4;
}
.footer-wrapper .r2 p {
  font-weight: 600;
