  flex: 2 1;
}
.footer-wrapper form {
  display: flex;
  /* grid-template-row: repeat(3, 1fr); */
  flex-direction: column;
}
.footer-wrapper .links {
  flex: 1 1;
}
.footer-wrapper .links ul {
  display: flex;
  flex-direction: column;
  margin-top: 1em;
  justify-content: space-between;
}
.footer-wrapper .links li {
  display: flex;
  flex-direction: column;
  text-transform: capitalize;
  font-size: 1.7em;
